.body {
  background-color: antiquewhite;
}
.center {
  display: flex;
  justify-content: center;
}

.container {
  position: relative;
  width: 500px;
  min-height: 1.5em;
  border: 0.05em solid #777;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 0.5em;
  border-radius: 0.25em;
  outline: none;
}

.container:focus {
  border-color: hsl(278, 100%, 50%);
}

.value {
  flex-grow: 1;
  display: flex;
  gap: 0.5em;
  flex-wrap: wrap;
}

.clearBtn {
  background: none;
  color: #e91b00;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  font-size: 1.25em;
}

.clearBtn:focus,
.clearBtn:hover {
  color: #333;
}

.divider {
  background-color: #777;
  align-self: stretch;
  width: 0.05em;
}

.caret {
  translate: 0 25%;
  border: 0.25em solid transparent;
  border-top-color: #777;
}

.options {
  position: absolute;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  max-height: 15em;
  overflow-y: auto;
  border: 0.05em solid #777;
  border-radius: 0.25em;
  width: 100%;
  left: 0;
  top: calc(100% + 0.25em);
  background-color: white;
  z-index: 100;
}

.options.show {
  display: block;
}

.option {
  padding: 0.25em 0.5em;
  cursor: pointer;
}

.option.selected {
  background-color: hsl(24, 100%, 70%);
}

.option.highlighted {
  background-color: hsl(36, 100%, 50%);
  color: white;
}

.optionBadge {
  display: flex;
  align-items: center;
  border: 0.05em solid #777;
  border-radius: 0.25em;
  padding: 0.15em 0.25em;
  gap: 0.25em;
  cursor: pointer;
  background: none;
  outline: none;
}

.optionBadge:hover,
.optionBadge:focus {
  background-color: hsl(29, 100%, 90%);
  border-color: hsl(0, 100%, 50%);
}

.optionBadge:hover > .removeBtn,
.optionBadge:focus > .removeBtn {
  color: hsl(0, 100%, 50%);
}

.optionBadge > .removeBtn {
  font-size: 1.25em;
  color: #777;
}

.footer {
  position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   background-color: rgb(204, 212, 212);
   text-align: center;
   padding: 20px 0;
}
a:link {
  text-decoration: none;
  color: #333;
}
